	</view>
</template>

<script>
	export default {
		
			
			// props是从父级（引用当前组件）的变量占位，在当前组件下给这些变量一个初始化值(initvalue)
			props: {
				title2:{
					type:String,
					default:''
				},
				lists:{
					type:Array,
					default(){
						return []
					}}
			},
			data() {
			return {
				title: 'Hello',
				newName: '',
				list: [{
					name: '周杰伦'
				}]
			}
		},
		onLoad() {},
		methods: {
			getMsg() {
				console.log("I am a demo")
				// 当前的this指的是该组件本身，指针指向当前组件。
				this.$emit('getMsg', this)
			}
		}
	}
</script>

